.sideright {
   margin: 0 auto;

   &__content {
      padding: 50px;
   }

   &__title {
      text-align: center;
   }
}

@media (max-width: 768px) {
   .sideright {
      width: 100%;
      height: 82vh;
      padding: 0;

      &__title {
         display: none;
      }
   }
}
